import { getRootUrl } from './rootUrl';
import axios from 'axios';




export const loadInspectionsByDateRange = (token, startDate, endDate) => {
  const rootUrl = getRootUrl().cm_url;
  const config = {
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  return axios.get(
    rootUrl + "/sales/getSalesInspectionSByDate?startDate="+startDate+"&endDate="+endDate,
    config).then(response => response.data)
};

